                    <figure class="image is-square grid-image" v-on:click="hideModal()">
                        <a v-bind:href="'#' + post.id">
                            <img v-bind:src="post['image']" alt="" v-bind:style="post['imageThumb']"/>
                        </a>
                    </figure>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "InstagramGrid",
        props: {
            isGridActive: Boolean,
            posts: Array
        },
        methods: {
            hideModal: function () {
                this.$emit('clickGridPost');
            }
        }
    }
</script>

